import "../App.css";
import React from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import Loading from "./loading";

function Articles({ articles, oldDate, setOldDate, page, setPage, trickOn }) {
  console.log(articles);

  /* new Date(oldDate.toISOString()).getTime() */
  let oneMoreOldDate = new Date(new Date(oldDate).getTime() - 86400000)
    .toISOString()
    .slice(0, 10);
  console.log("ez most a oneMoreOldDate", oneMoreOldDate);
  //ez most a oneMoreOldDate 2021-02-24

  const trick = (e) => {
    if (trickOn) {
      let item = document.getElementById(e.target.id);
      item.classList.toggle("trick");
    }
  };

  return (
    <InfiniteScroll
      dataLength={articles ? articles.length : 0} //This is important field to render the next data
      next={
        /* ()=>setOldDate(new Date(oneMoreOldDate).toISOString().slice(0, 10)) */
        () => setPage(page + 1)
      }
      hasMore={true}
      /* loader={<Loading />} */
      className="infinity-container"
      endMessage={
        <p style={{ textAlign: "center" }}>
          <b>Yay! You have seen it all</b>
        </p>
      }
    >
      {articles &&
        articles.map((article, index) => {
          return (
            <div
              className="article"
              id={"article" + index}
              key={"article" + index}
            >
              <h2 className="articleTitle">{article.title}</h2>
              <img
                className="articlePicture"
                src={article.urlToImage}
                alt={article.title}
              />
              <div className="articleContent">{article.description}</div>
              <div className="articleDate">
                {article.publishedAt.slice(0, 10)}
              </div>
              <a
                className="url"
                id={"url-" + index}
                onMouseEnter={trick}
                onMouseLeave={trick}
                href={article.url}
                target="_blank"
                rel="noreferrer"
              >
                Tovább a cikkhez..
              </a>
            </div>
          );
        })}
    </InfiniteScroll>
  );
}

export default Articles;
