import React from "react";
import { BrowserRouter as Router, Link, Redirect } from "react-router-dom";
import Loading from "./loading";

import CookieConsent from "react-cookie-consent";
import Title from "./navbar";

const LandingPage = () => {
  if (document.cookie !== "") {
    return <Redirect to="/" />;
  }

  return (
    <div id="landingPage">
      <Title klassz="landing-title"></Title>
      <div id="land-load">
        <Loading></Loading>
      </div>
      <CookieConsent
        location="bottom"
        buttonText={
          <Link to="/">
            <span id="cookieBtn">OK!</span>
          </Link>
        }
        cookieName="<cookieName>"
        style={{
          background: "#2B373B",
          height: window.screen.width < 416 ? "150px" : "65px",
          transition: "1s",
        }}
        contentStyle={{
          fontSize: window.screen.width < 416 ? "5vw" : "1.3em",
          color: "lightblue",
          textShadow: "0 0 1px red",
          textAlign: window.screen.width < 416 ? "center" : "start",
          padding: "5px",
          width: "50%",
        }}
        buttonStyle={{
          position: window.screen.width < 416 ? "relative" : "static",
          left: window.screen.width < 416 ? "48vw" : "0%",
          fontSize: "15px",
          boxShadow: "0 0 10px #4e503b",
          transform:
            window.screen.width < 416
              ? "scale(1.3) translate(-50%)"
              : "scale(1.3)",
          backgroundColor: "#87ff09",
          borderRadius: "10px",
        }}
        expires={150}
      >
        This website uses cookies to enhance the user experience.
        {/* <span style={{ fontSize: '10px' }}><subtext/link></span> */}
      </CookieConsent>
    </div>
  );
};

export default LandingPage;
